<template>
  <div>

    <div>
    <b-row>
      <b-col>
        <div class="d-flex">
          <div v-if="usuario.roles.name !== 'usuario'"
            class="text-center boton-cuadrado quinto c-hand text-white mt-3"
            data-toggle="modal"
            data-target="#modalAdd"
          >
            <i class="fas fa-plus"></i> <br />
            nuevo
          </div>

          <div
            class="text-center boton-cuadrado color-primary c-hand text-white mt-3 ml-3"
            data-toggle="modal"
            data-target="#modalSearch"
          >
          {{productos.length}}
            <br>
            <div v-if="productos.length === 1">producto</div>
            <div v-if="productos.length > 1">productos</div>
            <div v-if="productos.length ===0">productos</div>
            
          </div>
          <div class="d-flex align-items-center">
            <b-form-input
              class="w-75 ml-3"
              id="filter-input"
              v-model="filter"
              type="search"
              placeholder="Buscar"
            ></b-form-input>
          </div>
        </div>
      </b-col>
    </b-row>
    <b-row>
      <b-col sm="12">
        <div class="">
          <div class="text-center">
            <b-table
              class="card mt-3 list-scroll scrollbar-light-blue"
              :sticky-header="true"
              striped
              hover
              :filter="filter"
              :items="productos"
              :fields="fields"
            >
              <template #cell(precioBs)="row">
                {{ infoEmpresa.dolar * row.item.precio | formatNumber }}
              </template>
              <template #cell(funciones)="row" v-if="usuario.roles.name !== 'usuario'">
                <div
                  class="btn red-alert text-white mt-0 c-hand"
                  size="sm"
                  @click="deleteProducto(row.item._id)"
                >
                  <i class="fas fa-trash-alt"></i>
                </div>

                <div
                  @click="formData(row.item._id)"
                  class="btn yellow-danger text-white mt-0 c-hand"
                  size="sm"
                  data-toggle="modal"
                  data-target="#modalEdit"
                >
                  <i class="fas fa-pencil-alt"></i>
                </div>

                <!-- As `row.showDetails` is one-way, we call the toggleDetails function on @change -->
              </template>
            </b-table>
          </div>
        </div>
      </b-col>
    </b-row>
    <!-- modales  -->
    <!-- modal add  -->
    <div
      class="modal fade"
      id="modalAdd"
      tabindex="-1"
      role="dialog"
      aria-labelledby="myModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog modal-md" role="document">
        <div class="modal-content" :class="{ 'dark-secondary': dark }">
          <div
            class="modal-header text-center"
            :class="{ 'color-secondary text-white': dark }"
          >
            <h4 class="modal-title w-100 font-weight-bold">Agregar</h4>
            <button
              type="button"
              class="close"
              data-dismiss="modal"
              aria-label="Close"
            >
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body" :class="{ 'dark-secondary': dark }">
            <form id="formAdd" class="mx-3">
              <div class="form-group mb-2">
                <b-form-select
                  class="mt-3"
                  v-model="nuevoProducto.categoria"
                  :options="categorias"
                ></b-form-select>
                <b-form-select
                  class="mt-3"
                  v-model="nuevoProducto.unidadMedida"
                  :options="unidadesMedida"
                ></b-form-select>
                <b-form-select
                  class="mt-3"
                  v-model="nuevoProducto.proveedor_id"
                  :options="proveedores"
                ></b-form-select>
                <b-form-input
                  required
                  type="text"
                  onkeyup="validateNombre(this)"
                  id="nombre"
                  v-model="nuevoProducto.nombre"
                  placeholder="nombre"
                  class="mt-3"
                  autocomplete="off"
                />
                <b-form-input
                  required
                  type="text"
                  onkeyup="validateCod(this)"
                  style="text-transform: uppercase"
                  v-model="nuevoProducto.codigo"
                  placeholder="codigo"
                  class="form-control mt-3"
                  autocomplete="off"
                />

                <div class="d-flex">
                  <b-form-input
                    required
                    type="number"
                    v-model="nuevoProducto.cantidad"
                    placeholder="cantidad"
                    class="form-control mt-3  w-50"
                    autocomplete="off"
                  />

                  <b-form-input
                    required
                    type="number"
                    v-model="nuevoProducto.stock"
                    placeholder="stock"
                    class="form-control mt-3 ml-2 w-50"
                    autocomplete="off"
                  />
                  <b-form-input
                    required
                    type="number"
                    v-model="nuevoProducto.precio"
                    placeholder="precio"
                    class="form-control mt-3 ml-2 w-50"
                    autocomplete="off"
                  />
                </div>
                <b-form-textarea
                  required
                  type="text"
                  v-model="nuevoProducto.descripcion"
                  placeholder="descripcion"
                  class="form-control mt-3 ml-2"
                  autocomplete="off"
                  style="  border-radius: 0.25rem;
"
                />
              </div>
            </form>
          </div>
          <div class="modal-footer d-flex justify-content-center">
            <div
              @click="sendProduct()"
              id="btnPost"
              class="text-white mt-3 btn btn-block color-primary mr-4 ml-4 c-hand"
            >
              guardar
            </div>
          </div>
        </div>
      </div>
    </div>

    <!-- modal add  -->

    <!-- modal edit -->

    <div
      class="modal fade"
      id="modalEdit"
      tabindex="-1"
      role="dialog"
      aria-labelledby="myModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog modal-md" role="document">
        <div class="modal-content" :class="{ 'dark-secondary ': dark }">
          <div
            class="modal-header text-center"
            :class="{ 'color-secondary text-white': dark }"
          >
            <h4 class="modal-title w-100 font-weight-bold">Editar</h4>
            <button
              type="button"
              class="close"
              data-dismiss="modal"
              aria-label="Close"
            >
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body mx-3">
            <form
              id="formEdit
             "
              class="mx-3"
            >
              <div class="form-group mb-2" v-if="formVisibilityEDit === true">
                <b-form-select
                  class="mt-3"
                  v-model="formEdit.categoria"
                  :options="categorias"
                ></b-form-select>
                <b-form-select
                  class="mt-3"
                  v-model="formEdit.unidadMedida"
                  :options="unidadesMedida"
                ></b-form-select>
                <b-form-select
                  class="mt-3"
                  v-model="formEdit.proveedor_id"
                  :options="proveedores"
                ></b-form-select>
                <b-form-input
                  required
                  type="text"
                  v-model="formEdit.nombre"
                  placeholder="nombre"
                  class="mt-3"
                  autocomplete="off"
                />
                <b-form-input
                  required
                  type="text"
                  onkeyup="validateCod(this)"
                  style="text-transform: uppercase"
                  v-model="formEdit.codigo"
                  placeholder="codigo"
                  class="form-control mt-3"
                  autocomplete="off"
                />

                <div class="d-flex">
                  <b-form-input
                    required
                    type="number"
                    v-model="formEdit.cantidad"
                    placeholder="cantidad"
                    class="form-control mt-3  w-50"
                    autocomplete="off"
                  />
                  <b-form-input
                    required
                    type="number"
                    v-model="formEdit.stock"
                    placeholder="stock"
                    class="form-control mt-3 ml-2 w-50"
                    autocomplete="off"
                  />
                  <b-form-input
                    required
                    type="number"
                    v-model="formEdit.precio"
                    placeholder="precio"
                    class="form-control mt-3 ml-2 w-50"
                    autocomplete="off"
                  />
                </div>
                <b-form-textarea
                  required
                  type="text"
                  v-model="formEdit.descripcion"
                  placeholder="descripcion"
                  class="form-control mt-3 ml-2"
                  autocomplete="off"
                  style="  border-radius: 0.25rem;
"
                />
              </div>
            </form>
          </div>
          <div class="modal-footer d-flex justify-content-center mr-4 ml-4">
            <div
              @click="sendEdit(formEdit._id)"
              class="text-white mt-3 btn btn-block color-primary mr-4 ml-4 c-hand"
            >
              guardar
            </div>
          </div>
        </div>
      </div>
    </div>

    <!-- modal edit -->

    <!-- modales  -->
  </div>
  </div>
</template>

<script>
import numeral from "numeral";
import { mapState } from "vuex";
import axios from "axios";
import Vue from "vue";
Vue.filter("formatNumber", function(value) {
  return numeral(value).format("0,0"); // displaying other groupings/separators is possible, look at the docs
});
export default {
  created() {
    this.getCategorias();
    this.getUnidades();
    this.getProveedores();
    this.getProductos();
  },
  methods: {
    //solicitudes inmediatas
    async getUnidades() {
      const { data } = await axios.get(`${this.server}/system/unidades`);
      data.forEach((item) => {
        let data = { value: item._id, text: item.nombre };
        this.unidadesMedida.push(data);
      });
      let defaultItem = {
        value: null,
        text: "seleccione una unidad",
        disabled: true,
      };
      this.unidadesMedida.push(defaultItem);
    },
    async getCategorias() {
      const { data } = await axios.get(
        `${this.server}/system/categoriaProducto`
      );
      data.forEach((item) => {
        let data = { value: item._id, text: item.nombre };
        this.categorias.push(data);
      });
      let defaultItem = {
        value: null,
        text: "seleccione una categoria",
        disabled: true,
      };
      this.categorias.push(defaultItem);
    },
    async getProductos() {
      const { data } = await axios.get(`${this.server}/productos/get`);
      this.productos = data;
    },
    //solicitudes inmediatas

    async sendEdit(id) {
      const { data } = await axios.put(
        `${this.server}/productos/${id}`,
        this.formEdit,{headers:{xtoken: this.token}}
      );
      this.alert(data);
      if (data.value == true) return;
      if (data.value == false) return;
      this.formVisibilityEDit = false;
      this.formEdit.nombre = null;
      this.formEdit.codigo = null;
      this.formEdit.cantidad = null;
      this.formEdit.stock = null;
      this.formEdit.precio = null;
      this.formEdit.categoria = null;
      this.formEdit.unidadMedida = null;
      this.formEdit.proveedor_id = null;
      this.formEdit.unidadMedida = null;
      this.formEdit.descripcion = null;
      this.formEdit._id = null;
      this.getProductos();
    },

    async getProveedores() {
      const { data } = await axios.get(`${this.server}/proveedores/get`);
      data.forEach((item) => {
        let data = { value: item._id, text: item.nombre };
        this.proveedores.push(data);
      });
      let defaultItem = {
        value: null,
        text: "seleccione un proveedor",
        disabled: true,
      };
      this.proveedores.push(defaultItem);
    },
    async deleteProducto(id) {
      const { data } = await axios.delete(`${this.server}/productos/${id}`,{headers:{xtoken: this.token}});
      this.alert(data);
      if (data.value == false) {
        return;
      }
      this.getProductos();
    },
    async formData(id) {
      const { data } = await axios.get(`${this.server}/productos/${id}`);
      this.formVisibilityEDit = true;
      this.formEdit = data;
    },

    async sendProduct() {
      const { data } = await axios.post(
        `${this.server}/productos/`,
        this.nuevoProducto,{headers:{xtoken: this.token}}
      );
      this.alert(data);
      if (data.value == null) return;
      if (data.value == false) return;
      this.nuevoProducto.nombre = null;
      this.nuevoProducto.codigo = null;
      this.nuevoProducto.cantidad = null;
      this.nuevoProducto.stock = null;
      this.nuevoProducto.precio = null;
      this.nuevoProducto.categoria = null;
      this.nuevoProducto.unidadMedida = null;
      this.nuevoProducto.proveedor_id = null;
      this.nuevoProducto.unidadMedida = null;
      this.nuevoProducto.descripcion = null;
      this.getProductos();
    },
    alert(data) {
      if (data.value === true) {
        this.$toastr.success(data.message, "productos", this.options);
      }
      if (data.value === false) {
        this.$toastr.error(data.message, "productos", this.options);
      }
      if (data.value === null) {
        this.$toastr.warning(data.message, "productos", this.options);
      }
    },
  },

  computed: {
    sortOptions() {
      // Create an options list from our fields
      return this.fields
        .filter((f) => f.sortable)
        .map((f) => {
          return { text: f.label, value: f.key };
        });
    },
    ...mapState(["dark", "options", "server", "infoEmpresa",'token','usuario']),
  },

  data() {
    return {
      proveedores: [],
      filter: null,
      filterOn: [],
      fields: [
        {
          key: "nombre",
          sortable: true,
        },
        {
          key: "codigo",
          sortable: true,
        },
        {
          key: "proveedor_id.nombre",
          sortable: true,
          label: "proveedor",
        },
        {
          key: "categoria.nombre",
          label: "categoria",
          sortable: true,
        },
        {
          key: "precio",
          label: "precio",
          sortable: true,
        },
        {
          key: "categoria.nombre",
          label: "categoria",
          sortable: true,
        },
        {
          key: "unidadMedida.nombre",
          label: "unidad",
          sortable: true,
        },
        {
          key: "cantidad",
          label: "cantidad",
          sortable: true,
        },
        "precioBs",
        "funciones",
      ],

      inputEditVisibilyty: true,
      inputSearchVisibilyty: true,
      formVisibilityEDit: false,
      infoVisibilityDelete: false,
      infoVisibilityEdit: false,
      inputDeleteVisibilyty: true,

      formEdit: {
        nombre: null,
        codigo: null,
        stock: null,
        descripcion: null,
        categoria: null,
        unidadMedida: null,
        precio: null,
        proveedor_id: null,
        cantidad: null,
        createdAt: null,

        updatedAt: null,
        _id: null,
      },

      categorias: [],

      unidadesMedida: [],

      nuevoProducto: {
        nombre: null,
        codigo: null,
        stock: null,
        descripcion: null,
        categoria: null,
        unidadMedida: null,
        precio: null,
        proveedor_id: null,
        cantidad: null,
      },
      productos: [],
    };
  },
};
</script>
